import {
  addMonthsToDate,
  assignAdminNavbar,
  calculateAgeFromDOB,
  fetchUrl,
  formatTimestampToAMPM,
  getDateFromTimestamp,
  getToken,
  logoutBtnOnClick,
  redirectTo,
  removeOptions,
} from "../util";
import { ROLES } from "../util/constants";
import { initSearch } from "../util/search";
import {
  XANO_ADMIN_SIGN_IN_REDIRECT,
  XANO_ADMIN_USER_DETAIL_URL,
  XANO_ADMIN_UPDATE_USER_PAYMENT_URL,
} from "../util/xano_urls";

let UrlUserId, UserGymId;

window.onload = async function () {
  logoutBtnOnClick(XANO_ADMIN_SIGN_IN_REDIRECT, "sidebar-logout-btn");

  var curUrl = new URL(document.location.href);
  UrlUserId = curUrl.searchParams.get("userId") || null;

  if (!UrlUserId) {
    // ? TODO: Change this to a modal showcasing the error
    console.error("No user id found");
    return;
  }

 const token = getToken(); // ? get token (saved from signin page)
  if (token) {
    let result = await fetchUrl(XANO_ADMIN_USER_DETAIL_URL + UrlUserId, "GET", false, token);

    if (result.isFetchOk !== undefined && !result.isFetchOk) {
      console.error(result.message);
      // redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
      return;
    }

    const { team_role, ...data } = result;

    
    if (team_role && (team_role === ROLES.SUPER_ADMIN || team_role === ROLES.ADMIN)) {
      assignAdminNavbar(team_role);
      getSuperAdminData(data);
    } else {
      redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
    }
  } else {
    // ? if there is no token
    redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

async function getSuperAdminData(data) {
  const { gyms, user, payment_ledger } = data;
  UserGymId = user.gym_id;

  appendUserDetail(user, gyms);
  appendAttendedSessions(user.session_ledger);
  appendPaymentLedger(payment_ledger);
  initPaymentForm();
  initSearch({ mode: "client", clientObjRefName: "session_name", data: user.session_ledger }, appendAttendedSessions);
}
function appendUserDetail(data, gyms) {
  if (data.profile_picture) {
    $("#user-detail-pfp").css("background-image", `url(${data.profile_picture.url})`);
  }
  $("#user-detail-name").text(data.name);
  $("#user-detail-email").text(data.email);
  $("#user-detail-gender").text(data.gender);
  $("#user-detail-number").text(data.contact_number);
  $("#user-detail-weight").text(data.weight);
  $("#user-detail-height").text(data.height);
  $("#user-detail-dob").text(`${data.date_of_birth} (${calculateAgeFromDOB(data.date_of_birth)} Y/O)`);

  let selectGymEl = document.getElementById("user-detail-gym");
  removeOptions(selectGymEl);
  gyms.forEach((gym) => {
    var opt = document.createElement("option");
    opt.value = gym.id;
    opt.innerText = gym.location;
    if (gym.id === data.gym_id) opt.selected = true;
    selectGymEl.appendChild(opt);
  });

  $("#user-detail-membership").text(data.membership_status);
  // let selectGymMembership = document.getElementById("user-detail-membership");
  // removeOptions(selectGymMembership); // ? remove all webflow skeleton options
  // ["TRIAL", "INACTIVE", "1 month", "3 months", "6 months", "1 year"].forEach((t) => {
  //   var opt = document.createElement("option");
  //   opt.value = t;
  //   opt.innerText = t;
  //   if (t === data.membership_status) opt.selected = true;
  //   selectGymMembership.appendChild(opt);
  // });

  let selectFitnessEl = document.getElementById("user-detail-fitness-level");
  removeOptions(selectFitnessEl);
  ["Beginner", "Amateur", "Advanced", "Hardcore"].forEach((lvl) => {
    var opt = document.createElement("option");
    opt.value = lvl;
    opt.innerText = lvl;
    if (lvl === data.level) opt.selected = true;
    selectFitnessEl.appendChild(opt);
  });
}
function appendAttendedSessions(data) {
  const tableContainer = document.getElementById("table-container");
  const og_webflow_element = tableContainer.querySelector("#table-item");

  function appendAttendedSessionsFn(_sessions) {
    let allItems = tableContainer.querySelectorAll(".adb-user.w-inline-block");
    allItems.forEach((item, indx) => {
      if (indx !== allItems.length - 1) item.remove();
    });

    _sessions.forEach((session, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      clonedCard.setAttribute("href", `/admin/session?sessionId=${session.sessions_id}`);
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      el[1].innerText = session.session_name;
      el[2].innerText = session.name;
      el[3].innerText = session.code;
      el[4].innerText = getDateFromTimestamp(session.time);
      el[5].innerText = formatTimestampToAMPM(session.time, true);
      el[6].innerText = session?.users?.length || 0;
      el[7].innerText = session.status;

      clonedCard.style.display = "flex";
      tableContainer.prepend(clonedCard);
    });
  }

  appendAttendedSessionsFn(data);
  og_webflow_element.style.display = "none";
}
function appendPaymentLedger(data) {
  const tableContainer = document.getElementById("payment-container");
  const og_webflow_element = tableContainer.querySelector("#payment-item");

  function appendPaymentLedgerFn(_payments) {
    _payments.forEach((payment, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      el[0].innerText = payment.amount;
      el[1].innerText = payment.mode_of_payment;
      el[2].innerText = payment.membership_plan;
      el[3].innerText = getDateFromTimestamp(payment.time);
      el[4].innerText = payment.transaction_id || "NaN";

      clonedCard.style.display = "flex";
      tableContainer.prepend(clonedCard);
    });
  }

  appendPaymentLedgerFn(data);
  og_webflow_element.style.display = "none";
}
function initPaymentForm() {
  // const amount = document.getElementById("amount-input");
  // const transactionId = document.getElementById("transaction-id-input");
  const modeOfPayment = document.getElementById("mode-of-payment-select");
  const membershipPlan = document.getElementById("membership-plan-select");
  const selectDateEl = document.getElementById("datepicker-input");

  removeOptions(modeOfPayment);
  ["Cash", "Card", "Online"].forEach((t, indx) => {
    var opt = document.createElement("option");
    opt.value = t;
    opt.innerText = t;
    if (indx === 0) opt.selected = true;
    modeOfPayment.appendChild(opt);
  });

  removeOptions(membershipPlan);
  ["1 month", "3 months", "6 months", "1 year"].forEach((t, indx) => {
    var opt = document.createElement("option");
    opt.value = t;
    opt.innerText = t;
    if (indx === 1) opt.selected = true;
    membershipPlan.appendChild(opt);
  });

  // ? TODO: should I even allow them to pick a date??? is this even needed??
  $(selectDateEl)
    .datepicker({
      format: "dd/mm/yyyy",
      language: "en",
      multidate: false,
      orientation: "bottom auto",
    })
    .datepicker("setDate", "now");
}

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */

  $("form").on("submit", async function (e) {
    e.preventDefault();

    const $form = $(this); // The submitted form

    if ($form.attr("id") === "payment-form-block") {
     const token = getToken();
      if (token) {
        const amount = $form[0][0].value;
        const modeOfPayment = $form[0][1].value;
        const membershipPlan = $form[0][2].value;
        const selectDate = $form[0][3].value;
        const transactionId = $form[0][4].value;

        if (amount !== "" && modeOfPayment !== "" && membershipPlan !== "" && selectDate !== "") {
          let membershipMonth;
          switch (membershipPlan) {
            case "1 month":
              membershipMonth = 1;
              break;
            case "3 months":
              membershipMonth = 3;
              break;
            case "6 months":
              membershipMonth = 6;
              break;
            case "1 year":
              membershipMonth = 12;
              break;
          }
          let df = selectDate.split("/");

          let xano_input = {
            amount,
            transaction_id: transactionId || null,
            mode_of_payment: modeOfPayment,
            membership_plan: membershipPlan,
            membership_end_date: addMonthsToDate(new Date(), membershipMonth),
            time: new Date(`${df[2]}/${df[1]}/${df[0]} ${new Date().getHours()}:${new Date().getMinutes()}`).getTime(),
            gym_id: UserGymId,
          };

          let updatedUserPayment = await fetchUrl(
            XANO_ADMIN_UPDATE_USER_PAYMENT_URL + UrlUserId,
            "POST",
            xano_input,
            token
          );

          if (updatedUserPayment.isFetchOk !== undefined && !updatedUserPayment.isFetchOk) {
            console.error(updatedUserPayment.message);
            $form
              .siblings(".w-form-done")
              .hide() // Hide success
              .siblings(".w-form-fail")
              .show() // show failure
              .text("There was an error trying to add your session. Please refresh your browser and try again.");

            // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
            return;
          }
          $form
            .siblings(".w-form-done")
            .show() // show success
            .siblings(".w-form-fail")
            .hide(); // Hide failure

          window.location.reload();
        } else {
          console.error("Please fill in the required fields");
          $form
            .siblings(".w-form-done")
            .hide() // Hide success
            .siblings(".w-form-fail")
            .show() // show failure
            .text("Please fill in the first four fields");
        }
      } else {
        console.error("No token found");
        $form
          .siblings(".w-form-done")
          .hide() // Hide success
          .siblings(".w-form-fail")
          .show() // show failure
          .text("Please fill out all fields");
      }
    } else if ($form.attr("id") === "user-detail-form-block") {
     const token = getToken();
      if (token) {
        const gymId = document.getElementById("user-detail-gym").value;
        const fitnessLevel = document.getElementById("user-detail-fitness-level").value;

        let xano_input = {
          gym_id: gymId,
          level: fitnessLevel,
        };

        let updateUserDetail = await fetchUrl(XANO_ADMIN_USER_DETAIL_URL + UrlUserId, "POST", xano_input, token);

        if (updateUserDetail.isFetchOk !== undefined && !updateUserDetail.isFetchOk) {
          console.error(updateUserDetail.message);
          $form
            .siblings(".w-form-done")
            .hide() // Hide success
            .siblings(".w-form-fail")
            .show() // show failure
            .text("There was an error trying to add your session. Please refresh your browser and try again.");

          // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
          return;
        }

        $form
          .hide() // optional hiding of form
          .siblings(".w-form-done")
          .show() // Show success
          .siblings(".w-form-fail")
          .hide(); // Hide failure
        window.location.reload();
      } else {
        console.error("No token found");
        $form
          .siblings(".w-form-done")
          .hide() // Hide success
          .siblings(".w-form-fail")
          .show() // show failure
          .text("Please fill out all fields");
      }
    }
  });
});
